*{
  padding: 0;
  margin: 0;
}
h1{
  text-align: center;
  padding: 10px;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 23px;
}
h3{
  text-align: center;
}
.show-case{
  background-color: black;
  color: white;
  height: 94.5vh;
  position: relative;
  .show-case-context{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
